import { createStore } from 'vuex'
import createPersistedState from "vuex-persistedstate";
import ip from '/ipBackend.js'

const store = createStore({
  state () {
    return {
      ipBackend: ip,
      theme: {},
      master: {
        logo_app: '/images/logo-undip.png',
        name_app: 'PPID',
      },
      data_static: {
        status_pengaduan: [ //status laporan pengaduan & permohonan
          { value: 0 , text: 'Ditolak'},
          { value: 1 , text: 'Dibuat'},
          { value: 2 , text: 'Di Acc Operator'},
          { value: 3 , text: 'Di Acc Supervisor'},
          { value: 4 , text: 'Di Acc Manajer'},
        ],
        jenis_kategori: [ //kategori
          { value:'permohonan', text: 'PERMOHONAN' },
          { value:'pengaduan', text: 'PENGADUAN' },
        ],
        role: [ //user
          { value: 'operator', text: 'OPERATOR' },
          { value: 'supervisor', text: 'SUPERVISOR' },
          { value: 'manajer', text: 'MANAJER' },
          { value: 'pimpinan', text: 'PIMPINAN' },
        ],
        asal_pemohon: [ //asal pemohon laporan pengaduan & permohonan
          { value: 'dosen', text: 'DOSEN'},
          { value: 'tendik', text: 'TENDIK'},
          { value: 'mahasiswa', text: 'MAHASISWA'},
          { value: 'umum', text: 'UMUM'},
        ],
      },
      login: {},
      token: null,
      pagination: {
        row: 10,
        perPage: [10, 20, 50, 100],
      },
    }
  },
  mutations: {
    set_theme(state, val){
      state.theme = val
    },
    set_default(state, val){
      state.master = val.master
      state.pagination = val.pagination
      state.master = val.master
    },
    login(state, val) {
      state.login = val.data
      state.token = val.token
    },
    logout(state) {
      state.login = {}
      state.token = null
    },
  },
  plugins: [
    createPersistedState({
      key: "ppid",
      // paths: ['state'],
    }),
  ],
})
export default store