import { createRouter, createWebHistory } from 'vue-router';
import App from './App.vue';
import AppLanding from './AppLanding.vue';
import store from '@/store/store.js';

{/* <script src="https://www.google.com/recaptcha/api.js?render=6LdFq5UnAAAAAPLPZl-hOsunU19_4lnojMnV8iHf"></script> */}

const routes = [
  {
    path: '/',
    name: '',
    component: AppLanding,
    children: [
      {
        path: '',
        name: 'Detail Laporan',
        component: () => import('./pages/_/LaporanDetail.vue')
      },
      {
        path: '/login',
        name: 'Login',
        component: () => import('./pages/_/Login.vue')
      },
      {
        path: '/laporan',
        name: 'Form Laporan',
        component: () => import('./pages/_/Laporan.vue')
      },
      {
        path: '/error',
        name: 'Error',
        component: () => import('./pages/_/Error.vue')
      },
      {
        path: '/notfound',
        name: 'Notfound',
        component: () => import('./pages/_/NotFound.vue')
      },
      {
        path: '/access',
        name: 'Access',
        component: () => import('./pages/_/Access.vue')
      },
    ]
  },
  {
    path: '/',
    name: '',
    component: App,
    children: [
      {
        path: 'dashboard',
        name: 'Dashboard',
        component: () => import('./pages/_/Dashboard.vue'),
      },
      {
        path: 'pengaduan',
        name: 'Pengaduan',
        component: () => import('./pages/pengaduan/pengaduan.vue'),
      },
      {
        path: 'permohonan',
        name: 'Permohonan',
        component: () => import('./pages/permohonan/permohonan.vue'),
      },
    ],
    meta: {
      requiresAuth: true,
    },
  },
  {
    path: '/',
    name: 'Master',
    component: App,
    children: [
      {
        path: 'biro',
        name: 'Biro',
        component: () => import('./pages/biro/biro.vue')
      },
      {
        path: 'fakultas',
        name: 'Fakultas',
        component: () => import('./pages/fakultas/fakultas.vue')
      },
      {
        path: 'prodi',
        name: 'Prodi',
        component: () => import('./pages/prodi/prodi.vue')
      },
      {
        path: 'users',
        name: 'User',
        component: () => import('./pages/users/users.vue'),
        meta: {
          role: ['superuser', 'pimpinan']
        },
      },
      {
        path: 'kategori',
        name: 'Kategori',
        component: () => import('./pages/kategori/kategori.vue'),
        meta: {
          role: ['superuser', 'pimpinan', 'operator']
        },
      },
    ],
    meta: {
      requiresAuth: true,
    },
  },
];

const router = createRouter({
  history: createWebHistory(),
  routes,
});
// router.(() => {
//   console.log('jalan')
// })
router.beforeEach((to, from, next) => {
  console.log('jalan beforeEach')
  if(to.meta.requiresAuth && !store.state.token){
    next('/access');
  }else if(to.meta.requiresAuth && to.meta.role){
    let access = false
    for (let i  = 0; i < to.meta.role.length; i++) {
      const x = to.meta.role[i];
      if(store.state.login.role == x) access = true
    }
    if(access) next()
    else next('/access')
  }else if(to.name == undefined){
    next('notfound')
  }else{
    next()
  }
});
export default router;
